import React, { useState, useEffect } from "react";
import { Menu, X } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { Link } from "react-router-dom";

const products = {
  1: {
    name: "AVA 패키지",
    description: "AVA에서 사용 가능한 한정판 무기 아이템이 포함된 패키지입니다.",
    price: 9900,
    items: ["한정 무기", "보급 상자", "30일 사용권"],
  },
  2: {
    name: "슬러거 패키지",
    description: "슬러거 전용 골드 배트와 아이템 포함.",
    price: 7900,
    items: ["골드 배트", "치어리더 응원권", "15일 사용권"],
  },
  3: {
    name: "고스톱 패키지",
    description: "고스톱 전용 화투 아이템과 보너스 코인이 포함된 패키지입니다.",
    price: 8500,
    items: ["스페셜 화투", "보너스 코인 500개", "7일 이용권"],
  },
  4: {
    name: "포커 패키지",
    description: "포커 전용 테이블 스킨과 칩이 포함된 패키지입니다.",
    price: 8800,
    items: ["고급 테이블 스킨", "추가 칩 1000개", "15일 이용권"],
  },
};

const productImages = [
  "https://dl.gostop.pmang.cloud/upload/Dia_653_1707.png",
  "https://dl.gostop.pmang.cloud/upload/dia_smallbox.png",
  "https://dl.gostop.pmang.cloud/upload/Dia_652_1707.png",
  "https://dl.gostop.pmang.cloud/upload/Dia_651_1707.png",
  "https://dl.gostop.pmang.cloud/upload/Dia_750_1707.png"
];

function ProductCard({ id, onDetailClick }) {
  const imageIndex = (id - 1) % productImages.length;
  return (
    <div className="border rounded-lg shadow p-2 flex flex-col h-[320px]">
      <div className="w-full mb-2 flex-shrink-0">
        <img
          src={productImages[imageIndex]}
          alt="상품 이미지"
          className="w-full h-[190px] object-contain rounded"
        />
      </div>
      <div className="flex flex-col flex-grow">
        <h2 className="text-sm font-semibold mb-1">{products[id]?.name || `상품 이름 ${id}`}</h2>
        <p className="text-xs text-gray-500 mb-2 line-clamp-2">{products[id]?.description || "상품 설명 요약"}</p>
        <div className="mt-auto flex justify-between items-center">
          <span className="text-sm font-bold">₩{products[id]?.price.toLocaleString() || "9,900"}</span>
          <button
            onClick={() => onDetailClick(id)}
            className="text-xs px-2 py-1 bg-blue-500 text-white rounded"
          >
            상세보기
          </button>
        </div>
      </div>
    </div>
  );
}

function ProductModal({ id, onClose }) {
  const product = products[id];
  const imageIndex = (id - 1) % productImages.length;
  if (!product) return null;

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg shadow-lg p-6 max-w-md w-full relative">
        <button className="absolute top-2 right-2 text-gray-500" onClick={onClose}>
          ✕
        </button>
        <div className="aspect-w-1 aspect-h-1 w-full mb-4">
          <img
            src={productImages[imageIndex]}
            alt={product.name}
            className="w-full h-[300px] object-contain rounded"
          />
        </div>
        <h2 className="text-xl font-bold mb-2">{product.name}</h2>
        <p className="text-sm text-gray-600 mb-4">{product.description}</p>
        <ul className="text-sm text-gray-500 mb-4 list-disc pl-5">
          {product.items.map((item, index) => (
            <li key={index}>{item}</li>
          ))}
        </ul>
        <div className="flex justify-between items-center">
          <span className="text-lg font-bold">₩{product.price.toLocaleString()}</span>
          <button className="px-4 py-2 bg-green-500 text-white rounded">구매하기</button>
        </div>
      </div>
    </div>
  );
}

export default function ShopPage2() {
  const [menuOpen, setMenuOpen] = useState(false);
  const [category, setCategory] = useState("전체");
  const [bannerIndex, setBannerIndex] = useState(0);
  const [selectedProductId, setSelectedProductId] = useState(null);

  const pcCategories = ["AVA", "슬러거", "뉴맞고", "섯다", "7포커"];
  const mobileCategories = ["뉴맞고", "섯다", "7포커", "브라운더스트2"];
  const bannerImages = [
    "https://dl.poker.pmang.cloud/upload/pk_webmain_sales_2503_753x306.jpg",
    "https://dl.poker.pmang.cloud/upload/230614_pokerchipmion_main(753x306).jpg"
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      setBannerIndex((prevIndex) => (prevIndex + 1) % bannerImages.length);
    }, 4000);
    return () => clearInterval(interval);
  }, [bannerImages.length]);

  return (
    <div className="p-4 max-w-[1000px] w-full mx-auto relative">
      <nav className="bg-[#ffffff] shadow text-black flex items-center justify-between px-4 py-2 rounded mb-6">
        <div className="flex items-center gap-4">
          <a href="http://m.pmang.com/">
            <img
              src="data:image/png;base64,"
              alt="홈"
              className="h-5"
            />
          </a>
          <Link to="/" className="text-blue-500 hover:text-blue-700">
            첫 번째 페이지
          </Link>
        </div>
      </nav>

      <header className="flex justify-between items-center mb-6">
        <div className="text-center w-full">
          <h1 className="text-2xl font-bold">피망 웹상점</h1>
          <p className="text-sm text-gray-500">원하는 게임 상품을 손쉽게 찾아보세요</p>
        </div>
        <button
          className="absolute top-4 right-4 p-2"
          onClick={() => setMenuOpen(!menuOpen)}
        >
          {menuOpen ? <X size={24} /> : <Menu size={24} />}
        </button>
      </header>

      {menuOpen && (
        <div className="absolute right-4 top-20 bg-white border rounded shadow p-4 z-10 w-60">
          <h3 className="text-sm font-bold mb-2">PC 게임</h3>
          <ul className="mb-4 space-y-1">
            {pcCategories.map((item) => (
              <li
                key={item}
                className="text-sm cursor-pointer hover:underline"
                onClick={() => {
                  setCategory(item);
                  setMenuOpen(false);
                }}
              >
                {item}
              </li>
            ))}
          </ul>
          <h3 className="text-sm font-bold mb-2">모바일 게임</h3>
          <ul className="space-y-1">
            {mobileCategories.map((item) => (
              <li
                key={item}
                className="text-sm cursor-pointer hover:underline"
                onClick={() => {
                  setCategory(item);
                  setMenuOpen(false);
                }}
              >
                {item}
              </li>
            ))}
          </ul>
        </div>
      )}

      <div className="mb-2 text-sm text-gray-600">
        선택된 분류: <span className="font-semibold">{category}</span>
      </div>

      <div className="mb-4">
        <input
          type="text"
          placeholder="상품 검색..."
          className="w-full p-2 border rounded"
        />
      </div>

      <div className="mb-6 w-full relative bg-[#1a1a1a] rounded-lg shadow overflow-hidden">
        <div className="relative w-full" style={{ paddingBottom: 'calc(306 / 753 * 100%)' }}>
          <AnimatePresence mode="wait">
            <motion.img
              key={bannerImages[bannerIndex]}
              src={bannerImages[bannerIndex]}
              alt="추천 배너"
              className="absolute top-0 left-0 w-full h-full object-contain"
              initial={{ opacity: 0, x: 50 }}
              animate={{ opacity: 1, x: 0 }}
              exit={{ opacity: 0, x: -50 }}
              transition={{ duration: 0.5 }}
            />
          </AnimatePresence>
          <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2 flex space-x-2 z-10">
            {bannerImages.map((_, i) => (
              <div
                key={i}
                className={`w-3 h-3 rounded-full transition-all duration-300 ${
                  bannerIndex === i ? "bg-white" : "bg-white/50"
                }`}
                onClick={() => setBannerIndex(i)}
                style={{ cursor: 'pointer' }}
              />
            ))}
          </div>
        </div>
      </div>

      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 mb-6">
        <button className="p-2 border rounded shadow">전체</button>
        <button className="p-2 border rounded shadow">인기 상품</button>
        <button className="p-2 border rounded shadow">신규 상품</button>
        <button className="p-2 border rounded shadow">이벤트 상품</button>
      </div>

      <section className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
        {Array.from({ length: 20 }, (_, i) => i + 1).map((id) => (
          <ProductCard key={id} id={id} onDetailClick={setSelectedProductId} />
        ))}
      </section>

      {selectedProductId && (
        <ProductModal id={selectedProductId} onClose={() => setSelectedProductId(null)} />
      )}
    </div>
  );
} 